.App {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: #f06;
  background: linear-gradient(45deg, #ffc0cb, #ff26f9);
  min-height: 100%;
  width: 100vw;
  height: 100vh;
  position: relative;
  z-index: 1;
  box-shadow: inset -20px 0 38px -18px #ff26f9,
    inset -3px -13px 65px -18px yellow;
}
.App:before,
.App:after {
  content: "";
  position: absolute;
}
.App:before {
  background: red;
  box-shadow: 0 0 140px 64px red;
  z-index: 2;
  top: -96%;
  left: -72%;
  opacity: 0.8;
}
.App:after {
  background: white;
  z-index: 3;
  bottom: -96%;
  right: -72%;
  box-shadow: 0 0 140px 64px white;
  opacity: 1;
  border-radius: 100%;
}

.text {
  position: absolute;
  font-size: 3em;
  text-align: center;
  align-items: center;
  justify-content: center;
  font-family: "font_name";
  font-weight: bold;
  left: 5%;
}
.visible_text {
  color: transparent;
}

.image {
  width: 50%;
}
.image_2_left {
  position: absolute;
  transform: rotate(-90deg);
  top: 0;
  left: 25vw;
}
.image_2_right {
  position: absolute;
  transform: rotate(-90deg);
  bottom: 0;
  left: 25vw;
}

.container {
  display: flex;
  flex-direction: row;
  width: 90vw;
  transform: translate(-50%, -50%);
  animation: pulse-animation 1s ease-in-out infinite;
}

.container_2 {
  display: flex;
  flex-direction: row;
  width: 90vw;
}
@keyframes pulse-animation {
  0% {
    transform: scale(1);
  }
  20% {
    transform: scale(1.1);
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@font-face {
  font-family: "font_name";
  src: url(font.ttf);
}
